.main-container {
  height: 85vh;
  width: 90vw;
  /* max-width: 100%; */
}

.left-container {
  padding: 20px;
}

.right-container {
  height: 98%;
}

.console-container {
  height: 30%;
}

.console {
  line-height: 30% !important;
  padding-left: 5px;
  height: 65%;
  width: 80%;
  overflow-y: auto;
}

.console .error {
  color: red !important;
}

.console .success {
  color: green !important;
}

.console .info {
  color: grey !important;
}

.img-container {
  height: 65%;
  overflow-y: auto;
  padding: 12px;
}

.image-result {
  margin: 2px;
}

.total-text {
  margin: 5px 0px 5px 15px;
}

.form-control {
  min-width: 150px;
}

.button-select {
  margin-bottom: 5px !important;
}

.progress {
  width: 90%;
  height: 25px !important;
  background-color: white !important;
  border: solid 1px grey;
}

.MuiLinearProgress-barColorPrimary {
  background-color: green !important;
}

.MuiLinearProgress-barColorSecondary {
  background-color: grey !important;
}

.mint-container {
  width: calc(100vw - 10px) !important;
  height: 90vh;
  padding-left: 20px;
  /* padding: 20px; */
}

.mint-right-container {
  border-left: solid 1px rgb(220, 220, 220);
  height: 100%;
}

.mint-collection {
  width: 100%;
  height: 60%;
}

.mint-form {
  width: 100%;
  height: 40%;
}

.mint-title-box {
  height: 45%;
  line-height: 80%;
}

.mint-images-box {
  padding: 10px !important;
  height: 55%;
  width: 70%;
  border-top: solid 1px rgb(220, 220, 220);
  border-bottom: solid 1px rgb(220, 220, 220);
  overflow-y: auto;
}

.single-mint,
.batch-mint {
  display: flex;
  flex-direction: column;
  place-items: center;
}

.collection-search-box {
  text-align: center;
  height: 15%;
  border-bottom: solid 1px rgb(220, 220, 220);
}

.last-collections-box {
  text-align: center;
  height: 83%;
  overflow-y: auto;
}

.logo {
  max-width: 35px;
  max-height: 35px;
  margin-left: 15px;
}

.nav-link {
  color: wheat;
  text-decoration: none !important;
}

.nav-icons {
  display: flex;
  flex-direction: row;
  place-items: center;
  margin: auto 5px;
}

.right {
  margin-top: 12px !important;
  padding-right: 10px;
  /* padding-top: 10px; */
}

@media (max-width: 600px) {
  .App {
    max-width: 100vw;
  }
  .right-container {
    width: 100%;
  }

  .console-container {
    display: block !important;

    text-align: center;
  }

  .main-container {
    text-align: center;
  }

  .console {
    margin: 0px auto;
  }

  .mint-title-box {
    font-size: 0.6rem;
    line-height: 150%;
  }
  .mint-form {
    width: 100%;
  }
  .mint-right-container {
    display: flex;
    flex-direction: column;
    place-items: center;
    border-left: solid 0px rgb(220, 220, 220);
    border-top: solid 1px rgb(220, 220, 220);
    height: 100%;
  }
  .mint-container {
    padding: 15px;
  }

  .collection-search-box {
    margin-bottom: 20px;
  }
}

@keyframes cf3FadeInOut {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  20% {
    opacity: 0.2;
  }
  30% {
    opacity: 0.3;
  }
  40% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.5;
  }
  60% {
    opacity: 0.6;
  }
  70% {
    opacity: 0.7;
  }
  80% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

.minted-image {
  animation-name: cf3FadeInOut;
  animation-timing-function: ease-in-out;

  animation-duration: 5s;
}
